* {
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
  box-sizing: border-box;
  --mainColor1: teal;
  --mainColor2: #ffad7a;
  --mainColor2Hover: #ff6300;
  --mainColor1Hover: #00b9b9;
  --boxShadow: #999;
}

#error-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
}

.container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  justify-content: space-between;
}

nav {
  height: 5vh;
  position: fixed;
  width: 100%;
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 0 10px var(--boxShadow);
  z-index: 1;
}

.navbar-image {
  height: 4vh;
  margin-left: 100px;
}

nav ul {
  display: flex;
  margin-right: 200px;
  border-left: 1px solid #999;
  padding: 5px 0;
}

nav ul li {
  list-style: none;
}

nav ul li a {
  text-decoration: none;
  padding: 5px 20px;
  color: #333;
  border-right: 1px solid #999;
  transition: box-shadow 0.2s ease;
}

nav ul li a:hover {
  box-shadow: 0 0 5px var(--boxShadow);
}

nav ul li a:active {
  background-color: #ddd;
}

.login,
.register {
  background-color: var(--mainColor1);
  margin-left: 20px;
  text-align: center;
  text-decoration: none;
  border-radius: 50vh;
  color: #fff;
  font-size: 16px;
  box-shadow: 0 0 10px var(--boxShadow);
  transition: background-color 0.2s ease;
  border-right: none;
}

.login:hover,
.register:hover {
  background-color: var(--mainColor1Hover);
  box-shadow: 0 0 10px var(--boxShadow);
}

.login:active,
.register:active {
  color: #333;
}

.main-content-container {
  display: flex;
  width: 100%;
}

footer {
  height: 5vh;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 0 10px var(--boxShadow);
}

.main-container {
  padding: 50px 50px 50px 18vw;
  width: 100%;
  background-color: #eee;
  min-height: 95vh;
}

h1 {
  margin: 30px;
}
